/* Reset styles to avoid unwanted spacing */
body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
}

.main-header {
  position: relative;
  width: 100%;
}

.main-menu__list {
  display: flex; /* Default flex layout on larger screens */
  list-style: none;
  margin: 0;
  padding: 0;
}

.mobile-nav__toggler {
  display: none;
  background: none;
  border: none;
  font-size: 30px;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 999;
}

@media screen and (max-width: 991px) {
  .main-header-two__top,
  .main-header-two__bottom-right {
    display: none; /* Hide unnecessary elements */
  }
  li{
    color: #ffff
  }
  .main-menu__wrapper {
    padding: 10px 0;
  }

  /* Mobile Navbar styles */
  .main-menu__list {
    display: none; /* Hidden by default */
    position: fixed;
    top: 0;
    left: 0;
    width: 40%; /* Limit to 80% of the screen width */
    height: 100vh;
    background-color: var(--logistiq-black); /* Dark background color */
    /* padding: 60px 20px; */
    z-index: 1000;
    flex-direction: column; /* Column layout */
    align-items: center; /* Align items to the left */
    justify-content: flex-start; /* Don't use extra space */
    text-align: center;
    color: #ffff;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%); /* Initially off-screen */
  }

  .main-menu__list.open {
    display: flex;
    flex-direction: column;
    transform: translateX(0); /* Slide in when open */
  }

  /* Style for list items */
  .main-menu__list li {
    margin: 0; /* Remove spacing between items */
    padding: 10px 0; /* Adjust padding */
  }

  .main-menu__list li a link Link {
    font-size: 18px;
    text-decoration: none;
    color: var(--logistiq-base); /* White text */
    transition: color 0.3s ease;
    display: block; /* Ensure the anchor takes up full width for easier clicking */
  }

  .main-menu__list li a:hover {
    color: var(--logistiq-base);
  }

  /* Logo centered on mobile */
  .logo-box-two {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .logo-box-two img {
    max-width: 150px;
    height: auto;
  }

  /* Close button style */
  .close-btn {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 30px;
    color: white;
    background-color: white;
    border: none;
    cursor: pointer;
    z-index: 1001;
  }
  li{
    color:var(--logistiq-gray)
  }
}

/* Desktop Styles */
@media screen and (min-width: 992px) {
  .mobile-nav__toggler {
    display: none;
  }
li{
  color: #ffff
}
  .main-menu__list {
    display: flex ;
    flex-direction: row; /* Row layout on desktop */
  }
}

