/* Parent container to arrange team members in a single row */
.team-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; /* Allow wrapping for smaller screens */
    gap: 20px; /* Space between team member cards */
    justify-content: center; /* Center items horizontally */
    align-items: flex-start; /* Align items to the top */
    padding: 20px;
  }
  
  /* Individual team member card styling */
  .team-one__single {
    flex: 1 1 300px; /* Allow cards to resize responsively */
    max-width: 300px; /* Prevent cards from getting too wide */
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    overflow: visible; /* Ensure all content is visible */
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px;
  }
  
  /* Team member image styling */
  .team-one__single-img img {
    width: 100%;
    height: auto;
    border-radius: 50%; /* Optional: Circular image */
    margin-bottom: 15px;
  }
  
  /* Content container */
  .team-one__single-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  /* Title and role styling */
  .team-one__single-content h2 {
    font-size: 18px;
    font-weight: 600;
    color: #333;
    margin: 0;
  }
  
  .team-one__single-content span {
    font-size: 14px;
    font-weight: 500;
    color: #666;
  }
  
  /* Description text styling */
  .team-one__single-content p {
    font-size: 14px;
    line-height: 1.5;
    color: #777;
    margin: 0 0 10px 0;
  }
  
  /* Button box */
  .btn-box a {
    text-decoration: none;
    background: var(--logistiq-base);
    color: #ffffff;
    padding: 10px 15px;
    border-radius: 5px;
    transition: background 0.3s;
  }
  
  .btn-box a:hover {
    background: var(--logistiq-hover-base);
  }
  
  /* Responsive Breakpoints */
  
  /* For tablets (768px and above) */
  @media screen and (max-width: 768px) {
    .team-container {
      flex-wrap: wrap; /* Allow wrapping */
      gap: 15px;
    }
    .team-one__single {
      flex: 1 1 45%; /* Two cards per row */
      max-width: 45%;
    }
  }
  
  /* For small screens (480px and below) */
  @media screen and (max-width: 480px) {
    .team-container {
      flex-direction: column; /* Stack cards vertically */
      gap: 20px;
    }
    .team-one__single {
      flex: 1 1 100%; /* Full-width cards */
      max-width: 100%;
    }
  
    .team-one__single-content h2 {
      font-size: 16px; /* Adjust title size */
    }
  
    .team-one__single-content span,
    .team-one__single-content p {
      font-size: 13px; /* Adjust text size */
    }
  
    .btn-box a {
      padding: 8px 12px; /* Smaller button */
      font-size: 14px;
    }
  }
  